import * as React from "react";

export interface ButtonProps {
	primary?: boolean;
	backgroundColor?: string;
	size?: "small" | "medium" | "large";
	label: string;
	onClick?: () => void;
}

export function Button({
	primary = false,
	size = "medium",
	backgroundColor,
	label,
	...props
}: ButtonProps) {
	return (
		<button
			type="button"
			className="rounded-lg p-1 text-sm border-2"
			style={{ backgroundColor }}
			{...props}
		>
			{label}
		</button>
	);
}

Button.displayName = "Button";
